class Player {
    constructor(id, name, wins, status) {
        this._id = id;
        this._name = name;
        this._wins = wins;
        this._status = status;
    }
    id() {
        return this._id;
    }
    name() {
        return this._name;
    }
    wins() {
        return this._wins;
    }
    status() {
        return this._status;
    }
}
export { Player };
